:root{
    --facebook: #3b5999;
    --instagram:#e4405f;
    --twitter: #55acee;
}
.header{
    height: 80px;
}
.header a{
    text-decoration: none;
}
.Hnav{
    position: fixed;
    width: 100%;
    height: 80px;
    background-color: rgb(251, 251, 251);
    display: flex;
    z-index: 10;
}
.Hnav .Hnavlogo, .Hnav .Hnavlogo img{
    height: 80px;
    width: 80px;
}
.HnavDiv{
    height: 80px;
    margin: auto;
}
.HnavList{
    height: 100%;
    width: 100%;
    display: flex;
}
.HnavItem{
    height: 100%;
    margin: 0;
    list-style: none;
    position: relative;
}
.HnavLink{
    line-height: 80px;
    text-decoration: none;
    color: black;
    padding: 26px 20px 27px 20px;
    cursor: pointer;
}
.HnavItem > p{
    line-height: normal;
    padding: 26px 20px 27px 20px;
    margin: 0;
}
.containerModal{
    visibility: hidden;
    position: fixed;
    inset: 0 0 0 0;
    z-index: 99;
    width: 100%;
    height: 120%;
    background-color: rgba(0, 0, 0, .8);
    opacity: .0;
    transition: all 0.3s;
}
.activecontainerModal{
    visibility: visible;
    opacity: 1;
}
.Modal{
    position: absolute;
    inset: 20% 0 0 0;
    margin: 0 auto;
    background-color: rgb(245, 245, 245);
    width: 500px;
    height: -webkit-max-content;
    height: max-content;
    border-radius: 20px;
    padding: 5px 10px;
    z-index: 100;
    opacity: .0;
}
.activeModal{
    opacity: 1;
}
.Modal h6{
    font-size: 24px;
    text-align: center;
}
.Modal svg{
    position: absolute;
    right: 10px;
}
.Modal p{
    margin: 0;
    margin-bottom: 5px;
    width: 95%;
}
.contactanos{
    list-style: none;
    padding: 0;
}
.contactos, .contactos a{
    margin-bottom: 5px;
    color: #000;
    font-size: 18px;
}
.direccion:hover, .direccion:hover i{
    color: #276ae6;
    text-decoration: underline;
}
.whatsapp:hover, .whatsapp:hover i{
    color: #25D366;
    text-decoration: underline;
}
.celular:hover, .celular:hover i{
    color: #039237;
    text-decoration: underline;
}
.mail:hover, .mail:hover i{
    color: #c71a1a;
    text-decoration: underline;
}
.HiconsContact{
    width: 200px;
    margin: 0 auto 10px;
    display: flex;
    justify-content: space-between;
}
.HiconContact i{
    line-height: 40px;
    width: 40px;
    border-radius: 50%;
    color: #fff;
    text-align: center;
}
.HiconContact .fa-facebook{
    background-color: #3b5999;
    background-color: var(--facebook);
}
.HiconContact .fa-instagram{
    background-color: #e4405f;
    background-color: var(--instagram);
}.HiconContact .fa-twitter{
    background-color: #55acee;
    background-color: var(--twitter);
}
.Modal p{
    font-size: 18px;
}
.Modal form{
    display: flex;
    flex-direction: column;
}
.Modal form label{
    margin-top: 10px;
    font-size: 18px;
}
.Modal form input[type="text"]{
    font-size: 18px;
    border-radius: 5px;
    border: solid 1px black;
    padding: 0 5px;
}
.Modal form input[type="button"]{
    width: -webkit-min-content;
    width: min-content;
    padding: 0 10px;
    border: solid 1px rgb(182, 182, 182);
    border-radius: 5px;
    margin: 5px auto;
}
.Hdropdown{
    display: none;
    text-align: left;
    position: absolute;
    top: 75px;
    left: 20px;
    width: 150px;
    list-style: none;
    background-color: rgb(247, 247, 247);
    border-radius: 0 3% 3% 3%;
    padding-left: 10px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.20);
}
.HdropdowsItem{
    padding-top: 5px;
}
.HdropLink{
    margin: 0;
}
.HnavItem:hover .HnavLink{
    color: #548aff;
}
.HnavItem:hover > ul{
    display: block;
}
.HdropdowsItem:hover .HdropLink{
    color: #548aff;
}
.HdropdowsItem:hover > ul{
    display: block;
}
.Hicons{
    width: 190px;
    margin: auto 0;
    padding-top: 15px;
}
.HiconList:hover a{
    -webkit-filter: blur(2px);
            filter: blur(2px)
}
.HiconList{
    display: flex;
    padding: 0 10px 0 0;
}
.Hicon{
    margin: auto;
    list-style: none;
    text-align: center;
    transition: 0.2s;
}
.Hicon a{
    width: 50px;
    border-radius: 50%;
    display: block;
    text-decoration: none;
    color: black;
    line-height: 50px;
}
.Hicon a:hover{
    -webkit-transform: translateY(-3px) scale(1.05);
            transform: translateY(-3px) scale(1.05);
    box-shadow: 0 5px 5px 0px rgba(0, 0, 0, .4);
    -webkit-filter: none;
            filter: none;
    color: #fff;
}
.Hfacebook:hover{
    background: #3b5999;
    background: var(--facebook);
}
.Hinstagram:hover{
    background: #e4405f;
    background: var(--instagram);
}
.Htwitter:hover{
    background: #55acee;
    background: var(--twitter);
}
.Huser{
    width: 22px;
    margin-right: 28px;
}
.HuserList{
    display: block;
    margin: auto;
    list-style: none;
    padding: 0;
}
.HuserList a, .HuserList i{
    font-size: 25px;
    color: #3b5999;
}
.Hprofile:hover .HConfUser{
    display: block;
}
.HConfUser{
    display: none;
    position: absolute;
    right: 0;
    list-style: none;
    text-align: left;
    padding: 15px 25px 15px 15px;
    margin: 0;
    background-color: #f1f0f0;
    border-radius: 0 0 0 20px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.20);
}
.HConfUser p, .HConfUser p i{
    color: #000;
    margin-bottom: 5px;
}
.HConfUser li{
    cursor: pointer;
}
.HConfUser li:nth-child(2):hover{
    color: #276ae6;
}
.HConfUser li:nth-child(3):hover{
    color: red;
}
.HConfUser li:nth-child(4):hover{
    color: rgb(176, 32, 32);
}
.HdropdowsItem a{
    color: #000;
}
.HdropLink{
    cursor: pointer;
}
.Hcart{
    display: block;
    width: 60px;
    margin: auto 0;
    position: relative;
}
.HcartList{
    padding: 0;
    margin: auto;
    list-style: none;
    width: 55px;
    height: 30px;
    text-align: center;
}
.Hbuy-a{
    cursor: pointer;
    font-size: 25px;
    width: 48px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    text-decoration: none;
    color: #000;
    line-height: 45px;
}
.Hbuy-aColor{
    color: #636363;
}
.Hrow-product{
    max-height: 320px;
    overflow-y: scroll;
}
.Hrow-product::-webkit-scrollbar {
    width: 3px;
}
.Hrow-product::-webkit-scrollbar-thumb{
    background-color: #c4c4c4;
    border-radius: 50px;
}
.Hrow-product::-webkit-scrollbar-thumb:hover{
    background-color: #a5a5a5;
}
.Hcount-products{
    background-color: #000;
    color: #fff;
    padding: 9px;
    margin-top: auto;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
}
#Hcontador-productos{
    font-size: 12px;
}
.Hcount-productsColor{
    background-color: #636363;
}

.Hcontainer-cart-products{
    position: absolute;
    top: 80px;
    right: 0;
    background-color: #fff;
    width: 400px;
    z-index: 1;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.20);
    border-radius: 0 0 10px 10px;
}

.Hcart-product{
    display: flex;
    justify-content: space-between;
    padding: 20px 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.20);
}

.Hinfo-cart-product{
    display: flex;
    justify-content: space-between;
    flex: 0.95 1;
    grid-gap: 5px;
    gap: 5px;
}

.Htitulo-producto-carrito{
    font-size: 18px;
    margin: auto 0;
    margin-right: auto;
}

.Hprecio-producto-carrito{
    font-weight: 700;
    font-size: 20px;
    margin: auto 0 auto 5px;
}

.Hcantidad-producto-carrito{
    font-size: 20px;
    width: 40px;
    margin: auto 0;
}

.Hicon-close{
    width: 25px;
    height: 25px;
    margin: auto 0;
}

.Hicon-close:hover{
    stroke: red;
    cursor: pointer;
}

.Hcart-total{
    display: flex;
    justify-content: center;
    padding: 15px 0;
    grid-gap: 20px;
    gap: 20px;
}

.Hcart-total h3{
    font-size: 20px;
    font-weight: 700;
    margin: auto 0;
}

.Htotal-pagar{
    font-size: 20px;
    font-weight: 900;
}

.Hhidden-cart{
    display: none;
}

.Hcart-empty{
    padding: 20px 0 0 0;
    text-align: center;
}
.Hbtn-clear-all{
    border: none;
    background-color: #000;
    color: #fff;
    padding: 15px 0;
    display: block;
    width: 100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: inherit;
    cursor: pointer;
    font-size: 16px;
    transition: all 0.3s ease;
    margin: 0;
}
.Hbtn-clear-all:hover{
    background-color: #515151;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    border-radius: 10px;
}

.animation{
    position: absolute;
    right: 100%;
    place-items: end;
    display: grid;
}
#animado{
    height: -webkit-max-content;
    height: max-content;
    width: -webkit-max-content;
    width: max-content;
    color: transparent;
    font-size: 14px;
    font-weight: bold;
}
.animateUp{
    -webkit-animation: animateUp;
            animation: animateUp;
    -webkit-animation-duration: .4s;
            animation-duration: .4s;
}
@-webkit-keyframes animateUp {
    0%{-webkit-transform: translateY(10px);transform: translateY(10px);}
    100%{-webkit-transform: translateY(-30px);transform: translateY(-30px);
    Color: transparent;}
}
@keyframes animateUp {
    0%{-webkit-transform: translateY(10px);transform: translateY(10px);}
    100%{-webkit-transform: translateY(-30px);transform: translateY(-30px);
    Color: transparent;}
}
.animateDown{
    -webkit-animation: animateDown;
            animation: animateDown;
    -webkit-animation-duration: .4s;
            animation-duration: .4s;
}
@-webkit-keyframes animateDown {
    0%{-webkit-transform: translateY(-10px);transform: translateY(-10px);}
    100%{-webkit-transform: translateY(10px);transform: translateY(10px);
    Color: transparent;}
}
@keyframes animateDown {
    0%{-webkit-transform: translateY(-10px);transform: translateY(-10px);}
    100%{-webkit-transform: translateY(10px);transform: translateY(10px);
    Color: transparent;}
}

@media screen and (max-width: 1030px){
    .Hbars__menu{
        width: 30px;
        height: 30px;
        margin: auto 10px auto 0;
        cursor: pointer;
    }
    .Hbars__menu span{
        display: block;
        width: 100%;
        height: 2px;
        background-color: #000;
        margin-top: 6px;
        transition: all .5s;
    }
    .Hline2__bars-menu{
        position: relative;
        left: 0;
    }
    .HactiveLine1__bars-menu{
        -webkit-transform: rotate(45deg) translate(-2px, 1px);
                transform: rotate(45deg) translate(-2px, 1px);
        -webkit-transform-origin: 0px 100%;
                transform-origin: 0px 100%;
        translate: 5px;
    }
    .HactiveLine2__bars-menu{
        opacity: 0;
        position: relative;
        left: 30px;
    }
    .HactiveLine3__bars-menu{
        -webkit-transform: rotate(-45deg) translate(-4px, 2px);
                transform: rotate(-45deg) translate(-4px, 2px);
        -webkit-transform-origin: 0px 100%;
                transform-origin: 0px 100%;
        translate: 5px;
    }
    .HnavList{
        display: block;
        position: absolute;
        right: -100vw;
        transition: all .5s;
        top: 80px;
        height: calc(100vh - 80px);
        padding: 0;
        border-radius: 0 0 0 100%;
        text-align: center;
        width: 100%;
        padding-top: 10vh;
    }
    .HactiveNavList{
        background-color: #000;
        border-radius: 0 0 0 50%;
        translate: -100vw;
    }
    .HnavItem{
        margin: auto;
        height: 70px;
        width: 190px;
        list-style: none;
        padding: 0;
    }
    .HnavLink, .HdropLink{
        color: #fff;
    }
    .Hdropdown{
        visibility: hidden;
    }
    .HactiveDropdown{
        visibility: visible;
        text-align: left;
        position: relative;
        top: 0;
        left: 50px;
        width: 150px;
        list-style: none;
        background-color: transparent;
        border-radius: 0 3% 3% 3%;
        padding: 0;
        margin: auto;
    }
}
@media screen and (max-width: 600px){
    *{
        scroll-padding-top: 0;
    }
    .header{
        height: 40px;
    }
    .Hnav{
        height: 40px;
        transition: all .3s;
    }
    .HnavUp{
        -webkit-transform: translateY(-40px);
                transform: translateY(-40px);
    }
    .HnavDiv{
        height: 40px;
    }
    .Hnav .Hnavlogo, .Hnav .Hnavlogo img{
    height: 40px;
    width: 40px;
    }
    .HnavList{
        top: 40px;
        height: calc(100vh - 40px);
    }
    .Hcart{
        margin-right: 10px;
    }
    .Hrow-product{
        max-height: 250px;
    }
    .Hcontainer-cart-products{
        top: 40px;
        width: 100%;
    }
    .Hicons{
        width: 0;
    }
    .HactiveIcons{
        width: auto;
        margin: 0;
        padding: 0;
    }
    .HiconList{
        position: absolute;
        top: 55px;
        right: -100vw;
        transition: all 0.3s;
    }
    .HactiveIconList{
        right: 20px;
    }
    .HactiveIcon a{
        color: #fff;
    }
    .Huser{
        margin-right: 10px;
        display: flex;
    }
    .HuserList{
        display: flex;
        margin: auto 0;
    }
    .Hlogin{
        margin-right: 10px;
    }
}
@media screen and (max-width: 540px) {
    .infoContact{
        margin: 0 auto;
        width: 95%;
        padding: 5px 10px;
    }
    .infoContact h6{
        font-size: 19px;
    }
    .contactos, .contactos a{
        color: #000;
        font-size: 17px;
    }
}
@media screen and (max-width: 300px) {
    .Hcart-product{
        padding: 20px 5px;
    }
    .Hinfo-cart-product{
        flex: .95 1;
    }
}


